<template>
  <div class="history">
    <div
      v-show="showList"
      class="layer"
      @scroll.stop.prevent
      @touchmove.stop.prevent
    ></div>
    <div v-show="showList" class="list-log" @scroll.stop.prevent @touchmove.stop.prevent>
      <p>年龄</p>
      <p>身高</p>
      <p>学校</p>
      <p>期数</p>
      <p>家乡</p>
      <div>
        <el-input v-model="minAge" style="width: 45px" size="small" />
        ~
        <el-input v-model="maxAge" style="width: 45px" size="small" />
      </div>
      <div>
        <el-input v-model="minHeight" style="width: 45px" size="small" /> ~
        <el-input v-model="maxHeight" style="width: 45px" size="small" />
      </div>
      <el-input v-model="school" size="small" />
      <el-input
        v-model="episode"
        :placeholder="
          this.maxEpisode == undefined ? '至多' + 1 : '至多' + this.maxEpisode
        "
        size="small"
      />
      <el-input v-model="place" size="small" />
      <el-button v-on:click="search" class="confirm" type="primary">{{
        btnWord
      }}</el-button>
      <img v-on:click="close" class="close" src="../assets/关  闭.svg" />
    </div>
    <div class="list">
      <img v-on:click="openList" src="../assets/菜单.svg" />
      <p>筛选</p>
    </div>
    <ContentBox
      v-for="(item, index) in render"
      :key="index"
      :data="item"
      :openid="openid"
      :flag="getSexFlag(item.gender)"
    />
    <div class="nav">
      <button v-on:click="male" :class="sexFlag == 0 ? 'selected' : 'not-selected'">
        男神
      </button>
      <button v-on:click="female" :class="sexFlag == 1 ? 'selected' : 'not-selected'">
        女神
      </button>
    </div>
  </div>
</template>

<script>
import ContentBox from "../components/ContentBox";

export default {
  components: {
    ContentBox,
  },
  data: function () {
    return {
      btnWord: "筛选",
      minAge: "",
      maxAge: "",
      minHeight: "",
      maxHeight: "",
      maxEpisode: "",
      place: "",
      episode: "",
      school: "",
      showList: false,
      sexFlag: -1, //组件props的flag为1时渲染该组件,sexFlag不为-1时,传入flag为(index%2+sexFlag)故sexFlag为1时渲染女性,0时渲染男性
      openid: "tes", //当前观众的openid
      render: [
        {
          episode: 1,
          image: [
            `https://avatars2.githubusercontent.com/u/46736350?s=460&u=33838727dd329e3cbdb6337f6f56f04ddf647b27&v=4`,
            `https://avatars2.githubusercontent.com/u/46736350?s=460&u=33838727dd329e3cbdb6337f6f56f04ddf647b27&v=4`,
            `https://avatars2.githubusercontent.com/u/46736350?s=460&u=33838727dd329e3cbdb6337f6f56f04ddf647b27&v=4`,
          ],
          num: 1,
          nickName: "S微光F",
          school: "UESTc",
          age: "99",
          height: "150",
          constellation: "天蝎座",
          place: "place",
          introduction:
            "长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长",
          goal: "target",
          openid: "",
          uid: "",
          isLiked: false,
          likes: "999",
          connect: "",
          isConnect: false,
          gender: "女",
        },
        {
          episode: 1,
          image: [
            `https://avatars2.githubusercontent.com/u/46736350?s=460&u=33838727dd329e3cbdb6337f6f56f04ddf647b27&v=4`,
            `https://avatars2.githubusercontent.com/u/46736350?s=460&u=33838727dd329e3cbdb6337f6f56f04ddf647b27&v=4`,
            `https://avatars2.githubusercontent.com/u/46736350?s=460&u=33838727dd329e3cbdb6337f6f56f04ddf647b27&v=4`,
          ],
          num: 2,
          nickName: "S微光F",
          school: "UESTc",
          age: "99",
          height: "150",
          constellation: "天蝎座",
          place: "place",
          introduction:
            "长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长",
          goal: "target",
          openid: "",
          uid: "",
          isLiked: false,
          likes: "999",
          connect: "",
          isConnect: false,
          gender: "男",
        },
        {
          episode: 1,
          imgUrl: [
            `https://avatars2.githubusercontent.com/u/46736350?s=460&u=33838727dd329e3cbdb6337f6f56f04ddf647b27&v=4`,
            `https://avatars2.githubusercontent.com/u/46736350?s=460&u=33838727dd329e3cbdb6337f6f56f04ddf647b27&v=4`,
            `https://avatars2.githubusercontent.com/u/46736350?s=460&u=33838727dd329e3cbdb6337f6f56f04ddf647b27&v=4`,
          ],
          num: 3,
          nickName: "S微光F",
          school: "UESTc",
          age: "99",
          height: "150",
          constellation: "天蝎座",
          place: "place",
          about:
            "长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长长",
          target: "target",
          openid: "",
          uid: "",
          isLiked: false,
          likes: "999",
          connect: "",
          isConnect: false,
          gender: "女",
        },
      ],
    };
  },
  methods: {
    search: function () {
      this.btnWord = "正在筛选";
      this.axios
        .post("http://www.scgxtd.cn/public/user/history/search", {
          openid: this.openid,
          maxage: this.maxAge,
          minage: this.minAge,
          maxheight: this.maxHeight,
          minHeight: this.minHeight,
          place: this.place,
          episode: Number(this.episode),
          school: this.school,
        })
        .then((res) => {
          this.showList = false;
          this.btnWord = "筛选";
          if (res.data.code == 0) {
            this.render = res.data.data;
          } else if (res.data.code == -1) {
            alert("失败:" + res.data.msg);
          }
        });
    },
    openList: function () {
      this.showList = true;
    },
    close: function () {
      this.showList = false;
    },
    male: function () {
      if (this.$data.sexFlag == -1) {
        this.$data.sexFlag = 0;
      } else if (this.$data.sexFlag == 0) {
        this.$data.sexFlag = -1;
      } else if (this.$data.sexFlag == 1) {
        this.$data.sexFlag = 0;
      }
    }, //flag -1 时渲染所有actor, 通过三元表达式给组件的flag传入不同的值
    female: function () {
      if (this.$data.sexFlag == -1) {
        this.$data.sexFlag = 1;
      } else if (this.$data.sexFlag == 1) {
        this.$data.sexFlag = -1;
      } else if (this.$data.sexFlag == 0) {
        this.$data.sexFlag = 1;
      }
    },
    getSexFlag: function (g) {
      if (this.sexFlag == -1) {
        return 1;
      }
      if (this.sexFlag == 1) {
        if (g == "女") {
          return 1;
        } else if (g == "男") {
          return -1;
        }
      }
      if (this.sexFlag == 0) {
        if (g == "女") {
          return -1;
        } else if (g == "男") {
          return 1;
        }
      }
    },
  },
  mounted() {
    this.openid = localStorage.getItem("openid");
    this.axios.get("http://www.scgxtd.cn/public/user/history/episode").then((res) => {
      this.maxEpisode = res.data.data;
    }); //获取最大期数
    // this.axios.get("url", {
    //   //内容
    // }); //此处获取默认的历史推送信息:上一期的推送
  },
};
</script>

